    color: white;
}

#barra {
    background-color:#624193;
    border-radius: 15px
}

#barra a {
    color: white;
    text-transform: uppercase;
    font-size: 1.3rem;
}
#barra a:hover {
    color: #ecba16;
}
.slogan{
    font-size: 0.9rem;
    color:white;
    font-size: 1rem;
}

/* logo */

.titulo-logo {
    font-family: 'Pinyon Script', cursive;
    font-weight: bold;
    font-size: 2.8em;
    margin: 0px;
