.sample-flipbook{
	width:700px;
	height:450px;
	-webkit-transition:margin-left 0.2s;
	transition:margin-left 0.2s;
}

.sample-flipbook .page{
	width:700px;
	height:450px;
	background-color:white;
	/* line-height:300px; */
	font-size:1rem;
}

.sample-flipbook .page-wrapper{
	-webkit-perspective:2000px;
	-ms-perspective:2000px;
	-o-perspective:2000px;
	perspective:2000px;
}

.sample-flipbook .hard{
	/* background:#ccc !important; */
	color:#333;
	-o-box-shadow:inset 0 0 5px #666;
	-ms-box-shadow:inset 0 0 5px #666;
	box-shadow:inset 0 0 5px #666;
	font-weight:bold;
}

.sample-flipbook .odd{
	background:-webkit-gradient(linear, right top, left top, color-stop(0.95, #FFF), color-stop(1, #DADADA));
	background-image:-webkit-gradient(linear, right top, left top, color-stop(95%, #FFF), to(#C4C4C4));
	background-image:linear-gradient(right, #FFF 95%, #C4C4C4 100%);
	-o-box-shadow:inset 0 0 5px #666;
	-ms-box-shadow:inset 0 0 5px #666;
	box-shadow:inset 0 0 5px #666;
	
}

.sample-flipbook .even{
	background:-webkit-gradient(linear, left top, right top, color-stop(0.95, #fff), color-stop(1, #dadada));
	background-image:-webkit-gradient(linear, left top, right top, color-stop(95%, #fff), to(#dadada));
	background-image:linear-gradient(left, #fff 95%, #dadada 100%);
	-o-box-shadow:inset 0 0 5px #666;
	-ms-box-shadow:inset 0 0 5px #666;
	box-shadow:inset 0 0 5px #666;
} 

