/* menu lateral (izquierda) */
.sidebar-content{
    list-style-type: none;
    height: 95vh;
    position:sticky;
    top: 15px;
    padding-left: 0px;
    margin-top: 0px;   
    overflow-x: auto;
}



/* Primer nivel */
.sidebar-content .list-group-item:hover {
    cursor: pointer;
    background: var(--primary-color);
}

.sidebar-content .list-group-item:hover a {
    color: white;
}

/* Segundo nivel */
.sidebar-content .level .list-group-item {
    padding-left: 25px;
}

/* Tercer nivel */
.sidebar-content .level .list-group-item {
    padding-left: 50px;
}


.sidebar-content a {
    text-decoration: none;
    width: 100%;
    display: block;
}


