.tarjetas-unidades {
  display: grid;
  grid-template-columns: repeat(1,1fr);
  transform-origin: top left;
  transform: skewY(4.398705355deg);
  margin: 20vw 25% 0 2%;
  list-style: none;
  color: white;
}
.tarjetas-unidades a {
  color: white;
}
.tarjetas-unidades li{
  transform-origin: top left;
  transform: skewY(-4.398705355deg) rotatez(-14.03624deg);
  padding-top: 150%;
  z-index: 1;
  transition-property: z-index;
  transition-duration: .3s;
  margin-top:20px;
  
}
.details{
  position: absolute;
  z-index: 2;
  width: 98%;
  height: 95%;
  top: 0;
  background-color: #53b3cb;
  border-radius: 2vw;
  -webkit-filter: drop-shadow(2px 6px 3px rgba(0, 0, 0, 0.4));
  filter: drop-shadow(2px 6px 3px rgba(0, 0, 0, 0.4));
  transform: rotate(0deg) translate(0, 0);
  transition-property: transform;
  transition-duration: .3s;
}



.tarjetas-unidades li:nth-child(2n) .details, .tarjetas-unidades li:nth-child(2n) .details > div{
  background-color: var(--secondary-alternative-color);
}
.tarjetas-unidades li:nth-child(2n+1) .details, .tarjetas-unidades li:nth-child(2n+1) .details > div{
  background-color:var(--secondary-color);
}
/* .tarjetas-unidades li:nth-child(3n+3) .details, .tarjetas-unidades li:nth-child(3n+3) .details > div{
  background-color: #53b3cb;
} */
.product{
  position: absolute;
  z-index: 3;
  width: 88%;
  height: 91%;
  top: 0;
  transform: translateX(6.5%) translateY(5%);
  border-radius: 1.2vw;
  background:#ffff;
  overflow: hidden;
}
.tarjetas-unidades h2, .tarjetas-unidades p, .tarjetas-unidades img {
  position: absolute;
  z-index: 4;
}
.tarjetas-unidades h2,  .tarjetas-unidades p {
  background-color: #53b3cb;
  padding: 0.4em;
}
.tarjetas-unidades li:nth-child(2n) h2, .tarjetas-unidades li:nth-child(2n) p{
  background-color: var(--secondary-alternative-color);
}
.tarjetas-unidades li:nth-child(2n+1) h2,.tarjetas-unidades li:nth-child(2n+1) p{
  background-color: var(--secondary-color);
}

.tarjetas-unidades h2{
  top: 3.5%;
  left: 4%;
  text-transform: uppercase;
  font-weight: bold;
}
.tarjetas-unidades p {
  bottom: 3.5%;
  right: 5%;
  font-size: 1.2em;
}
.tarjetas-unidades img{
  height: 30%;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%) translateY(-2%);
  transition-property: height,transform;
  transition-duration: .3s;
}

.tarjetas-unidades li:hover{ 
  z-index: 4;
}
.tarjetas-unidades li:hover .details{ 
  transform: rotate(14.03624deg) translate(0%, 10%) scale(1.3);
}
.tarjetas-unidades li:hover img{ 
  height: 35%;
  transform: translateX(-50%) translateY(-14%);
}

@media (min-width:300px) { 
  body{
    font-size: 2.75vw;
  }
  .tarjetas-unidades {
    grid-template-columns: repeat(1,1fr);
    grid-column-gap: 3vw;
    margin: 10vw 14vw 0 2vw;
  }
}

@media (min-width:600px) { 
  body{
    font-size: 1.95vw;
  }
  .tarjetas-unidades {
    grid-template-columns: repeat(1,1fr);
    grid-column-gap: 2vw;
    margin: 8vw 10vw 0 1vw;
  }
}

@media (min-width:900px) { 
  body{
    font-size: 1.5vw;
  }
  .tarjetas-unidades {
    grid-template-columns: repeat(3,1fr);
    grid-column-gap: 1.5vw;
    margin: 6vw 7.5vw 0 0.5vw;
  }
}

@media (min-width:1200px) { 
  body{
    font-size: 1.25vw;
  }
  .tarjetas-unidades {
    grid-template-columns: repeat(3,1fr);
    grid-column-gap: 1vw;
    margin: 4.5vw 6vw 0 0.5vw;
  }
}

@media (min-width:1500px) { 
  body{
    font-size: 1vw;
  }
  .tarjetas-unidades {
    grid-template-columns: repeat(3,1fr);
    grid-column-gap: 1vw;
    margin: 4vw 6vw 0 0.5vw;
  }
}


