@import '../globals.css';

.menu-superior {
    border-bottom: 4px solid var(--primary-color);
    background: var(--primary-color);
    background-image: url("./Fondo-superior.png")
}

.marca {
    /* color: var(--secondary-color); */
    font-weight: bold;
    color: white;
}

#barra {
    background-color:#624193;
    border-radius: 15px
}

#barra a {
    color: white;
    text-transform: uppercase;
    font-size: 1.3rem;
}
#barra a:hover {
    color: #ecba16;
}
.slogan{
    font-size: 0.9rem;
    color:white;
    font-size: 1rem;
}

/* logo */

.titulo-logo {
    font-family: 'Pinyon Script', cursive;
    font-weight: bold;
    font-size: 2.8em;
    margin: 0px;
    position: relative;
    bottom: 25px;
    color: white;
   
}
.img-logo{
    max-width: 150px;
}
.slogan-logo{
    font-size: 1.4em;
    font-family: 'Niconne', cursive;
    position: relative;
    bottom: 30px;
    margin: 0px;
    color: white;
}
#navbarNav {
justify-content: center;
}